@font-face {
    font-family: 'Varela Round', sans-serif;
    src: url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
}
.userPhoto{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    margin-top: 20px;
    padding: 2px;
    background: #10ab9e;
}

.selectedPage{
    font-weight: bold;
    font-size: large;
}

.usersContainer{
    display: flex;
    width: 100%;
    height: 100%;
    margin-top: 10px;
    flex-wrap: wrap;
    gap: 5px;
    justify-content: space-between;
    align-items: center;
}

.user{
    display: flex;
    min-width: 150px;
    max-width: 150px;
    min-height: 200px;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;
    font-weight: bold;
    padding: 7px;
    font-family: 'Varela Round', sans-serif;
}



.name{
    flex-direction: column;
    flex-wrap: wrap;
    display: flex;
    text-align: center;
    gap:10px;
}

.paginator{
    display: flex;
    justify-content: center;
    align-items: center;
}

